.user-nav {
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  margin-top: 11px;

  @media (max-width: $tablet-width) {
    align-items: center;
    width: auto;
    margin-top: 0;
  }
}

.user-nav__link {
  display: flex;
  max-width: 100px;
  margin-right: 35px;

  text-align: center;

  &:last-child {
    margin-right: 0;
  }

  &:hover {
    color: $color-brown;
  }

  &:active {
    text-decoration: underline;
  }

  @media (max-width: $tablet-width) {
    margin-left: 5px;
    padding: 0;
  }
}

.user-nav__link-login {
  @media (max-width: $tablet-width) {
    display: none;
  }
}

.user-nav__link-cart span {
  display: flex;

  &:first-child {
    margin-right: 7px;
  }

  @media (max-width: $tablet-width) {
    display: none;
  }
}

.user-nav__link-cart svg {
  display: none;

  @media (max-width: $tablet-width) {
    display: flex;
    width: 20px;
    height: 18px;
  }
}
